.Question {
}

.questions {
  display: flex;
  flex-flow: column;
  align-items: center;
}

.question {
  flex: 1 1 auto;
  width: 100%;
  margin: 0.4em;
  padding: 0.5em 1.25em;
  border-radius: 4px;
  color: #eee;
  background-color: #4c4c4c;
  cursor: pointer;
}

.question.active {
  background-color: darkcyan;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main {
  font-size: 1.2rem;
  max-width: 400px;
  margin: 1rem auto;
}

button {
  width: 100%;
  margin: 2em;
  padding: 0.6em 1.25em;
  font-size: 1.2rem;
  border-radius: 4px;
  color: #eee;
  cursor: pointer;
  border: none;
  outline: none;
  background-color: hsla(180, 100%, 20%, 1);
  box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.3);
}

button:disabled {
  background-color: hsla(180, 100%, 15%, 1);
  box-shadow: none;
  color: #ccc;
  cursor: not-allowed;
}

button:active {
  background-color: hsla(180, 100%, 28%, 1);
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.5);
}

